.content {
    display: flex;
    position: relative;
    margin-top: 100px;
}

.content-right {
    padding: 40px 0px;
    width: 59%;
}

.content-left {
    width: 41%;
}

.content-left img {
    padding: 40px 100px;
    width: 300px;
    height: 300px;
    border-radius: 50%;
}

.content-right hr {
    width: 40px;
    margin-left: 0px;
    border: 1px solid #0916ee;
    border-radius: 5px;
}

.content-right .introduction h1 span {
    color: #0916ee;
}

.content-right p.description span {
    font-size: 20px;
}

.content-right .buttons {
    margin-top: 50px;
}

.content-right .buttons a {
    color: black;
    text-align: center;
    padding: 10px 15px;
    text-decoration: none;
}

.content-right .buttons a.contactme {
    color: #ffff;
    background-color: #0916ee;
    border-radius: 20px;
}