.header {
    display: flex;
    background-color: #ffff;
    margin-top: 40px;
    justify-content: space-around;
}

.header a {
    color: black;
    text-align: center;
    padding: 8px 15px;
    text-decoration: none;
}

.header-left {
    width: 40%;
}

.header-right {
    width: 60%;
}

.header-left i.coffee-icon {
    border-radius: 20px;
    padding: 6px 8px;
    background-color: #5059ea;
}

.header-left svg {
    color: #fff;
}

.header-left a.title {
    font-size: 18px;
    font-weight: bold;
}

.header-left a.title span {
    color: #0916ee;
}

.header-right .resume {
    background-color: #5059ea;
    margin-left: 50px;
    color: #fff;
    border-radius: 20px;
}

.header-right .resume svg {
    color: #fff;
}

.header-right a:hover {
    background-color: #d7d8e4;
    color: #000 !important;
    border-radius: 20px;
}

.header-right a.active {
    color: #0916ee;
}
  